import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    bgcMark:"home",
    username:'Login',
    maxpage:0,
    whatPage:1,
    videoObj:[]
  },
  mutations: {
    getUserName(state,e){
      state.username = e;
    },
    changeBgc(state,e){
      state.bgcMark =e;
    },
    getVideoObj(state,e,){
      console.log("哪一页:"+state.whatPage);
      state.videoObj[state.whatPage-1]=e;
    },
    getMaxPage(state,e){
      state.maxpage = e;
    },
    getWhatPage(state,e){
      state.whatPage =e;
    },
    cleanUndefined(state){
      for(let i=0 ;i<=state.maxpage-1;++i){
        if(state.videoObj[i]===undefined){
          state.videoObj[i]=[];
        }
      }

    }
  },
  actions: {
  },
  modules: {
  }
})
